import Pinchable from "./pinchable";
import Rotatable from "./rotatable";
import Draggable from "./draggable";
import Resizable from "./resizable";
import Scalable from "./scalable";
import Warpable from "./warpable";
import Snappable from "./snappable";
import DragArea from "./drag-area";
import Origin from "./Origin";
import Scrollable from "./scrollable";
import Default from "./default";
import Padding from "./padding";
import Clippable from "./clippable";
import OriginDraggable from "./origin-draggable";
import Roundable from "./roundable";
import Groupable from "./groupable";
import BeforeRenderable from "./before-renderable";
import Renderable from "./renderable";
import Clickable from "./clickable";
import edgeDraggable from "./edge-draggable";
import IndividualGroupable from "./individual-groupable";
import { camelize, pushSet } from "../../utility";
import { Able } from "../types";

export const MOVEABLE_ABLES = /*#__PURE__*/ [
  BeforeRenderable,
  Default,
  Snappable,
  Pinchable,
  Draggable,
  edgeDraggable,
  Resizable,
  Scalable,
  Warpable,
  Rotatable,
  Scrollable,
  Padding,
  Origin,
  OriginDraggable,
  Clippable,
  Roundable,
  Groupable,
  IndividualGroupable,
  Clickable,
  DragArea,
  Renderable,
] as const;

export const MOVEABLE_EVENTS = /*#__PURE__*/ (
  MOVEABLE_ABLES as readonly Able[]
).reduce((current, able) => {
  (able.events || []).forEach((name) => {
    pushSet(current, name);
  });
  return current;
}, [] as any[]) as Array<(typeof MOVEABLE_ABLES)[number]["events"][number]>;

export const MOVEABLE_PROPS = /*#__PURE__*/ (
  MOVEABLE_ABLES as readonly Able[]
).reduce((current, able) => {
  (able.props || []).forEach((name) => {
    pushSet(current, name);
  });
  return current;
}, [] as any[]) as Array<(typeof MOVEABLE_ABLES)[number]["props"][number]>;

export const MOVEABLE_REACT_EVENTS = /*#__PURE__*/ MOVEABLE_EVENTS.map(
  (name) => {
    return camelize(`on ${name}`);
  }
) as Array<`on${Capitalize<
  (typeof MOVEABLE_ABLES)[number]["events"][number]
>}`>;
