type Key = string | number;
type ChangedItem = [number, number];
type ChangedBeforeAdded = [number, number];
type List<ItemType> = ItemType[];

class PolyMap<K extends Key, V> {
  private keys: K[] = [];
  private values: V[] = [];

  get(key: K): V | undefined {
    const index = this.keys.indexOf(key);
    return index !== -1 ? this.values[index] : undefined;
  }

  set(key: K, value: V): void {
    const index = this.keys.indexOf(key);
    if (index === -1) {
      this.keys.push(key);
      this.values.push(value);
    } else {
      this.values[index] = value;
    }
  }
}

class HashMap<V> {
  private object: Record<Key, V> = {};

  get(key: Key): V | undefined {
    return this.object[key];
  }

  set(key: Key, value: V): void {
    this.object[key] = value;
  }
}

const SUPPORT_MAP = typeof Map === "function";

class Link {
  prev?: Link;
  next?: Link;

  connect(prevLink?: Link, nextLink?: Link): void {
    this.prev = prevLink;
    this.next = nextLink;
    if (prevLink) prevLink.next = this;
    if (nextLink) nextLink.prev = this;
  }

  disconnect(): void {
    const { prev, next } = this;
    if (prev) prev.next = next;
    if (next) next.prev = prev;
  }

  getIndex(): number {
    let index = -1;
    let link: Link | undefined = this;

    while (link) {
      link = link.prev;
      ++index;
    }
    return index;
  }
}

function orderChanged(
  changed: ChangedBeforeAdded[],
  fixed: boolean[]
): ChangedItem[] {
  const fromLinks: Link[] = [];
  const toLinks: Link[] = [];

  changed.forEach(([from, to]) => {
    const link = new Link();
    fromLinks[from] = link;
    toLinks[to] = link;
  });

  fromLinks.forEach((link, i) => {
    link.connect(fromLinks[i - 1]);
  });

  return changed
    .filter((_, i) => !fixed[i])
    .map(([from, to]) => {
      if (from === to) {
        return [0, 0];
      }

      const fromLink = fromLinks[from];
      const toLink = toLinks[to - 1];
      const fromIndex = fromLink.getIndex();

      fromLink.disconnect();
      fromLink.connect(toLink, toLink?.next);

      const toIndex = fromLink.getIndex();
      return [fromIndex, toIndex];
    });
}

class Result<ItemType> {
  prevList: List<ItemType>;
  list: List<ItemType>;
  added: number[];
  removed: number[];
  changed: ChangedItem[];
  maintained: ChangedItem[];
  changedBeforeAdded: ChangedBeforeAdded[];
  fixed: boolean[];

  private cacheOrdered?: ChangedItem[];
  private cachePureChanged?: ChangedItem[];

  constructor(
    prevList: List<ItemType>,
    list: List<ItemType>,
    added: number[],
    removed: number[],
    changed: ChangedItem[],
    maintained: ChangedItem[],
    changedBeforeAdded: ChangedBeforeAdded[],
    fixed: boolean[]
  ) {
    this.prevList = prevList;
    this.list = list;
    this.added = added;
    this.removed = removed;
    this.changed = changed;
    this.maintained = maintained;
    this.changedBeforeAdded = changedBeforeAdded;
    this.fixed = fixed;
  }

  get ordered(): ChangedItem[] {
    if (!this.cacheOrdered) {
      this.calculateOrdered();
    }
    return this.cacheOrdered!;
  }

  get pureChanged(): ChangedItem[] {
    if (!this.cachePureChanged) {
      this.calculateOrdered();
    }
    return this.cachePureChanged!;
  }

  private calculateOrdered(): void {
    const ordered = orderChanged(this.changedBeforeAdded, this.fixed);
    const pureChanged: ChangedItem[] = [];

    this.cacheOrdered = ordered.filter(([_from, _to], i) => {
      const [fromBefore, toBefore] = this.changed[i];
      if (_from !== _to) {
        pureChanged.push([fromBefore, toBefore]);
        return true;
      }
      return false;
    });

    this.cachePureChanged = pureChanged;
  }
}

type MapConstructor<K, V> = new () => {
  get(key: K): V | undefined;
  set(key: K, value: V): void;
};

export function diff<ItemType>(
  prevList: List<ItemType>,
  list: List<ItemType>,
  findKeyCallback?: (e: ItemType) => Key
): Result<ItemType> {
  const mapClass: MapConstructor<unknown, number> = SUPPORT_MAP
    ? Map
    : findKeyCallback
    ? HashMap
    : PolyMap;
  const callback = findKeyCallback || ((e: ItemType) => e as unknown as Key);

  const added: number[] = [];
  const removed: number[] = [];
  const maintained: ChangedItem[] = [];
  const prevKeys = prevList.map(callback);
  const keys = list.map(callback);
  const prevKeyMap = new mapClass();
  const keyMap = new mapClass();
  const changedBeforeAdded: ChangedBeforeAdded[] = [];
  const fixed: boolean[] = [];
  const removedMap: Record<number, number> = {};
  const changed: ChangedItem[] = [];
  let addedCount = 0;
  let removedCount = 0;

  prevKeys.forEach((key, prevListIndex) => {
    prevKeyMap.set(key, prevListIndex);
  });

  keys.forEach((key, listIndex) => {
    keyMap.set(key, listIndex);
  });

  prevKeys.forEach((key, prevListIndex) => {
    const listIndex = keyMap.get(key);
    if (listIndex === undefined) {
      removed.push(prevListIndex);
      ++removedCount;
    } else {
      removedMap[listIndex] = removedCount;
    }
  });

  keys.forEach((key, listIndex) => {
    const prevListIndex = prevKeyMap.get(key);
    if (prevListIndex === undefined) {
      added.push(listIndex);
      ++addedCount;
    } else {
      maintained.push([prevListIndex, listIndex]);
      removedCount = removedMap[listIndex] || 0;
      changedBeforeAdded.push([
        prevListIndex - removedCount,
        listIndex - addedCount,
      ]);
      fixed.push(listIndex === prevListIndex);

      if (prevListIndex !== listIndex) {
        changed.push([prevListIndex, listIndex]);
      }
    }
  });

  removed.reverse();
  return new Result(
    prevList,
    list,
    added,
    removed,
    changed,
    maintained,
    changedBeforeAdded,
    fixed
  );
}

class ListDiffer<ItemType> {
  private findKeyCallback: ((e: ItemType) => Key) | undefined;
  private list: List<ItemType>;

  constructor(
    list: List<ItemType> = [],
    findKeyCallback?: (e: ItemType) => Key
  ) {
    this.findKeyCallback = findKeyCallback;
    this.list = [...list];
  }

  update(list: List<ItemType>): Result<ItemType> {
    const newData = [...list];
    const result = diff(this.list, newData, this.findKeyCallback);
    this.list = newData;
    return result;
  }

  static diff = diff;
}

export default ListDiffer;
