import { IEventMap } from "./types";
import { isObject } from "./utils";

/**
* Checks if the specified class value exists in the element's class attribute.
* @memberof DOM
* @param element - target
* @param className - the class name to search
* @return {boolean} return false if the class is not found.
* @example
import {hasClass} from "utility";

console.log(hasClass(element, "start")); // true or false
*/
export function hasClass(element: Element, className: string) {
  if (element.classList) {
    return element.classList.contains(className);
  }
  return !!element.className.match(new RegExp(`(\\s|^)${className}(\\s|$)`));
}

/**
* Add the specified class value. If these classe already exist in the element's class attribute they are ignored.
* @memberof DOM
* @param element - target
* @param className - the class name to add
* @example
import {addClass} from "utility";

addClass(element, "start");
*/
export function addClass(element: Element, className: string) {
  if (element.classList) {
    element.classList.add(className);
  } else {
    element.className += ` ${className}`;
  }
}

/**
* Removes the specified class value.
* @memberof DOM
* @param element - target
* @param className - the class name to remove
* @example
import {removeClass} from "utility";

removeClass(element, "start");
*/
export function removeClass(element: Element, className: string) {
  if (element.classList) {
    element.classList.remove(className);
  } else {
    const reg = new RegExp(`(\\s|^)${className}(\\s|$)`);

    element.className = element.className.replace(reg, " ");
  }
}

export function getDocument(el?: Node) {
  return el?.ownerDocument || document;
}

export function getDocumentElement(el?: Node) {
  return getDocument(el).documentElement;
}

export function getDocumentBody(el?: Node) {
  return getDocument(el).body;
}

export function isWindow(val: any): val is Window {
  return val && "postMessage" in val && "blur" in val && "self" in val;
}

export function isNode(el?: any): el is Node {
  return (
    isObject(el) &&
    el.nodeName &&
    el.nodeType &&
    "parentNode" in el &&
    "ownerDocument" in el
  );
}

export function getWindow(el?: Node) {
  return el?.ownerDocument?.defaultView || window;
}

export function addEvent<K extends keyof IEventMap>(
  el: EventTarget,
  type: K,
  listener: (e: IEventMap[K]) => void,
  options?: boolean | AddEventListenerOptions
): void;
/**
  * Sets up a function that will be called whenever the specified event is delivered to the target
  * @memberof DOM
  * @param - event target
  * @param - A case-sensitive string representing the event type to listen for.
  * @param - The object which receives a notification (an object that implements the Event interface) when an event of the specified type occurs
  * @param - An options object that specifies characteristics about the event listener.
  * @example
  import {addEvent} from "utility";
  
  addEvent(el, "click", e => {
    console.log(e);
  });
  */
export function addEvent(
  el: EventTarget,
  type: string,
  listener: (e: Event) => void,
  options?: boolean | AddEventListenerOptions
) {
  el.addEventListener(type, listener, options);
}

export function removeEvent<K extends keyof IEventMap>(
  el: EventTarget,
  type: K,
  listener: (e: IEventMap[K]) => void,
  options?: boolean | EventListenerOptions
): void;
/**
  * removes from the EventTarget an event listener previously registered with EventTarget.addEventListener()
  * @memberof DOM
  * @param - event target
  * @param - A case-sensitive string representing the event type to listen for.
  * @param - The EventListener function of the event handler to remove from the event target.
  * @param - An options object that specifies characteristics about the event listener.
  * @example
  import {addEvent, removeEvent} from "utility";
  const listener = e => {
    console.log(e);
  };
  addEvent(el, "click", listener);
  removeEvent(el, "click", listener);
  */
export function removeEvent(
  el: EventTarget,
  type: string,
  listener: (e: Event) => void,
  options?: boolean | EventListenerOptions
) {
  el.removeEventListener(type, listener, options);
}
